  outline: none;
}
.ubc-accordion-tabs__tab-list li a[role=tab]::before, .ubc-accordion-tabs__tab-list li button[role=tab]::before {
  content: "";
  display: block;
  height: var(--ubc-tabs-block-tab-nav-top-border-height, 5px);
  position: absolute;
  transition: 0.1s ease-in-out;
  background: var(--ubc-tabs-block-tab-nav-top-border-background, #0075af);
  left: -2px;
  right: -2px;
  top: -2px;
}
.ubc-accordion-tabs__tabs-panel {
  display: block;
  margin: 0;
  padding: 0;
}
